"use client";

import Link from "next/link";
import { useEffect, useState } from "react";
import Image from "next/image";
import data from "@/../public/data/categories";
import { Swiper, SwiperSlide } from "swiper/react";
import { Data, Product } from "../../../types/index";

import "swiper/css";

const TechnoOff = () => {

    const inOffProducts: Product[] = data.products.filter((product) => product.discount.isDiscount);
    console.log(inOffProducts);
    <div className="story_container my-4 px-2 lg:px-6">
        <Swiper
            spaceBetween={20}
            slidesPerView={1.5}
            breakpoints={{
                640: { slidesPerView: 2 },
                768: { slidesPerView: 3 },
                1024: { slidesPerView: 3.5 },
            }}
        >
            {inOffProducts.map((product) => (
                <SwiperSlide key={product.id}>
                    <Link
                        href={`/products/${product.id}`}
                        className="category_link flex flex-col justify-center items-center space-y-2.5 focus:border-sky-700 focus:border-2 rounded-full focus:outline-dashed w-full focus:outline-2"
                    >
                        <div className="image_container relative w-24 md:w-32 lg:w-32 md:h-32 lg:h-32 h-24 border-2 rounded-full border-sky-500 p-1">
                            <Image
                                src={product.images.find((image) => image.main === true)?.src || "@/img/default/default-featured-image.png.jpg"}
                                alt={product.name}
                                fill={true}
                                className="category_image p-1"
                            />
                        </div>
                        <p className="text-[.735rem] sm:text-[.900rem] lg:text-[.920rem] text-center">
                            {product.name}
                        </p>
                    </Link>
                </SwiperSlide>
            ))}
        </Swiper>
    </div>
}


export default TechnoOff;